.main {
    width: 400px;
    height: 400px;
    margin: 0 auto;
    /* border: 1px solid green; */
    position: absolute;
    left: 30px;
    top: 30px;
    z-index: 3;
}
.wheelOuter{
    width: 460px;
    height: 460px;
    background-image: url("../../../../../assets/roulette/rouletteWheel.png");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    position: absolute;
    left: 0px;
    top: 0px;
    z-index: 3;

}
.plate {
    background-color: #009700;
    width: 350px;
    height: 350px;
    margin: 25px;
    border-radius: 50%;
    position: relative;
    -webkit-animation: rotate 20s infinite linear;
    animation: rotate 20s infinite linear;
}
.plate:after,
.plate:before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
}
.plate:after {
    top: 6px;
    right: 6px;
    bottom: 6px;
    left: 6px;
}
.plate:before {
    background: rgba(0, 0, 0, 0.65);
    border: 1px solid silver;
    box-shadow: inset 0px 0px 0px 2px #808080;
    top: 17%;
    left: 17%;
    right: 17%;
    bottom: 17%;
    z-index: 1;
}
.number {
    width: 32px;
    height: 175px;
    display: inline-block;
    text-align: center;
    position: absolute;
    top: 0;
    left: calc(50% - 16px);
    transform-origin: 50% 100%;
    background-color: transparent;
    border-left: 16px solid transparent;
    border-right: 16px solid transparent;
    border-top: 175px solid black;
    box-sizing: border-box;
}
.number:nth-child(odd) {
    border-top-color: red;
}
.number:nth-child(37) {
    border-top-color: green;
}
.pit {
    color: #fff;
    padding-top: 18px;
    width: 32px;
    display: inline-block;
    font-size: 12px;
    transform: scale(1, 1.8);
    position: absolute;
    top: -175px;
    left: -16px;
}
.inner {
    display: block;
    height: 350px;
    /* width: 350px; */
    position: relative;
    
}
.inner:after,
.inner:before {
    content: "";
    display: block;
    position: absolute;
    border-radius: 50%;
}
.inner:after {
    z-index: 3;
    top: 24%;
    right: 24%;
    bottom: 24%;
    left: 24%;
    background-color: #4d4d4d;
    border: 3px solid #808080;
}
.inner:before {
    top: 15%;
    bottom: 15%;
    left: 15%;
    right: 15%; 
    content: "";
    color: #fff;
    font-size: 60px;
    z-index: 5;
    border-radius: 50%;
}
.inner.rest:before {
    transition: top 0.5s ease-in, right 0.5s ease-in, bottom 0.5s ease-in, left 0.5s ease-in;
    top: 22%;
    bottom: 22%;
    left: 22%;
    right: 22%;

}

