.btn {
    background: green;
    border: 1px solid #000000;
    border-top-color: #00b300;
    border-left-color: #00b300;
    padding: 0;
    margin: 0;
    display: inline-block;
    font-size: 15px;
    width: 175px;
    padding: 12px;
    margin: 12px;
    cursor: pointer;
    color: #fff;
    font-family: "Roboto", sans-serif;
    border-radius: 3px;
}
.btn .btn-label {
    padding: 12px;
    white-space: nowrap;
}
.btn.btn-reset {
    background: darkgreen;
    border: 1px solid #000000;
    border-top-color: #009700;
    border-left-color: #009700;
}
.btn.disabled {
    opacity: 0.2;
    transition: opacity 0.24s linear;
    cursor: not-allowed;
}
@-webkit-keyframes rotate {
    0% {
        transform: rotateZ(0deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
}
@keyframes rotate {
    0% {
        transform: rotateZ(0deg);
    }
    100% {
        transform: rotateZ(360deg);
    }
}
.data {
    /* display: block; */
    width: 150px;
    height: 150px;
    background-color: rgba(0, 0, 0, 0.65);
    position: absolute;
    top: 155px;
    left: 155px;
    /* animation: rotate 24s reverse linear infinite; */
    /* perspective: 2000px; */
    z-index: 100;
    border: 1px solid #000000;
    border-radius: 50%;
}
.data .data-inner {
    position: relative;
    width: 100%;
    height: 100%;
    text-align: center;
    transition: transform 0.72s;
    transform-style: preserve-3d;
}
.data.reveal .data-inner {
    transform: rotateY(180deg);
}

.data .mask,
.data .result {
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    position: absolute;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    border-radius: 50%;
    overflow: hidden;
    display: inline-block;
}
.data .mask {
    color: #fff;
    font-size: 24px;
    margin: auto;
    line-height: 1.4;
    padding-top: 36px;
}
.data .result {
    background-color: green;
    color: white;
    transform: rotateY(180deg);
    align-items: center;
    color: #fff;
}
.data .result-number {
    font-size: 72px;
    font-weight: 500;
    line-height: 1.2;
    margin-top: 12px;
}
.data .result-color {
    text-transform: uppercase;
    font-size: 21px;
    line-height: 1;
}
.previous-results {
    max-width: 350px;
    margin: 24px 12px;
}
.previous-list {
    perspective: 2000;
}
.previous-result {
    display: flex;
    margin: 1px;
    padding: 12px;
    color: #fff;
}
.previous-result:first-child {
    -webkit-animation: flipin 0.48s ease-out;
    animation: flipin 0.48s ease-out;
}
.previous-result .previous-number {
    flex: 1;
}
.color-green {
    background-color: green;
}
.color-black {
    background-color: black;
}
.color-red {
    background-color: red;
}
.visuallyhidden {
    position: absolute;
    overflow: hidden;
    clip: rect(0 0 0 0);
    height: 1px;
    width: 1px;
    margin: -1px;
    padding: 0;
    border: 0;
}
@-webkit-keyframes flipin {
    0% {
        transform: rotateX(90deg);
    }
    100% {
        transform: rotateX(0deg);
    }
}
@keyframes flipin {
    0% {
        transform: rotateX(90deg);
    }
    100% {
        transform: rotateX(0deg);
    }
}
@keyframes ball-role{
    0% {top:13%; bottom:13%; left:13%; right:13%}
    100% {top:20%; bottom:20%; left:20%; right:20%}
}